import { __decorate } from "tslib";
import { Component, Vue } from 'vue-property-decorator';
import page from '@/components/common-components/page.vue';
import rightBox from '@/components/common-components/right-box.vue';
import editBox from '@/components/common-components/edit-box.vue';
import { deepClone } from '@/until';
let businessManagement = class businessManagement extends Vue {
    constructor() {
        super(...arguments);
        this.showEditbox = false;
        this.showAddNav = false;
        this.pageTitle = '附加费设置';
        this.sultoptions = [{
                label: '外卖',
                value: 'delivery'
            }, {
                label: '自提',
                value: 'selflifting'
            }];
        // 附加费收费方式
        this.options2 = [
            {
                label: '按费用',
                value: 'bycost'
            }
        ];
        this.data = [];
        this.currentIndex = 0;
        this.modelIndex = 0;
        this.headImgList = [];
        this.model = {
            startPoint: 0,
            critical: 0
        };
        this.imageList = [];
        this.isEdit = false;
        this.fields = [];
        this.surcharge = [];
        this.surchargeConfig = [
            {
                label: '',
                trigger: 'bycost',
                rule: [
                    {
                        section: [0, 0],
                        fee: 0
                    }
                ],
                sultType: []
            }
        ];
        this.savefields = [
            {
                type: 'submit',
                label: '保存'
            }
        ];
        this.options = {
            scrollToInvalidField: true,
            layout: 'classic' // classic fresh
        };
    }
    get shopId() {
        return this.$route.params.id;
    }
    get shopName() {
        return this.$route.query.shopName;
    }
    created() {
        let shopData = deepClone(this.$route.params);
        this.pageTitle = shopData.title;
        this.initConfig(shopData);
    }
    addLadder() {
        this.surchargeConfig.push({
            label: '',
            trigger: 'bycost',
            rule: [
                {
                    section: [0, 0],
                    fee: 0
                }
            ],
            sultType: []
        });
    }
    addRule(index) {
        if (this.surchargeConfig[index].rule && this.surchargeConfig[index].rule.length > 0) {
            let fee = this.surchargeConfig[index].rule[this.surchargeConfig[index].rule.length - 1].section[1];
            this.surchargeConfig[index].rule.push({
                section: [fee, 0],
                fee: 0
            });
        }
        else {
            this.surchargeConfig[index].rule.push({
                section: [0, 0],
                fee: 0
            });
        }
    }
    // ----------------计算属性-----------------------
    get schoolInfo() {
        return this.$store.state.schoolInfo;
    }
    // ----------------监听方法-----------------------
    // ----------------调用方法-----------------------
    resetHandler() {
        this.$emit('cancel');
    }
    submitHandler(e) {
        e.preventDefault();
        let state = this.ladderValidate();
        if (!state) {
            return;
        }
        let updateData = {
            extracharge: this.surchargeConfig
        };
        this.$store.dispatch('updateShopInfoByObj', {
            shopId: this.shopId,
            value: updateData
        }).then((e) => {
            if (e.success) {
                this.$createDialog({
                    type: 'alert',
                    content: '保存成功',
                    onConfirm: (e) => {
                        this.$router.push({
                            name: 'businessManagement',
                            params: {
                                id: this.shopId,
                                oprate: 'update',
                                data: updateData
                            }
                        });
                    }
                }).show();
            }
        });
    }
    ladderValidate() {
        let state = true;
        this.surchargeConfig.some(item => {
            if (!item.label) {
                const toast = this.$createToast({
                    txt: '请输入附加费名称',
                    type: 'error'
                });
                toast.show();
                state = false;
                return true;
            }
            let rules = item.rule;
            if (rules.length === 0) {
                const toast = this.$createToast({
                    txt: '请添加费用规则',
                    type: 'error'
                });
                toast.show();
                state = false;
                return true;
            }
            else {
                let ruleState = false;
                let currentRule = null;
                rules.some((ruleItem, index) => {
                    if ((rules.length - 1) !== index) {
                        if (ruleItem.section[1] <= ruleItem.section[0]) {
                            const toast = this.$createToast({
                                txt: '梯费用结束值值需大于开始值',
                                type: 'error'
                            });
                            toast.show();
                            state = false;
                            return true;
                        }
                    }
                    if (currentRule) {
                        if (currentRule.section[1] !== ruleItem.section[0]) {
                            ruleState = true;
                            const toast = this.$createToast({
                                txt: '后阶梯费用开始值需等于前阶梯费用结束值',
                                type: 'error'
                            });
                            toast.show();
                            state = false;
                            return true;
                        }
                    }
                    currentRule = ruleItem;
                });
                if (ruleState) {
                    return true;
                }
            }
        });
        return state;
    }
    showToastType() {
        const toast = this.$createToast({
            txt: '保存成功',
            type: 'correct'
        });
        toast.show();
    }
    deleteConfig(index) {
        this.surchargeConfig.splice(index, 1);
    }
    deleteSection(index, ruleIndex) {
        this.surchargeConfig[index].rule.splice(ruleIndex, 1);
    }
    validateHandler() {
    }
    initConfig(shopData) {
        if (shopData && shopData.extracharge) {
            this.surchargeConfig = shopData.extracharge.map((item) => {
                return {
                    label: item.label,
                    trigger: item.trigger,
                    rule: item.rule,
                    sultType: item.sultType || []
                };
            });
        }
    }
};
businessManagement = __decorate([
    Component({
        components: {
            page,
            rightBox,
            editBox
        }
    })
], businessManagement);
export default businessManagement;
